import React, { useState } from 'react';
import { getStripe } from '../utils/get-stripe';
import { items } from '../data/items';

type Items = typeof items;

const Checkout = () => {
  const [loading, setLoading] = useState(false);
  const [cart, setCart] = useState<Items>([]);

  const redirectToCheckout = async (event: any) => {
    event.preventDefault();
    if(cart.length) {
      setLoading(true);

      const stripe = await getStripe();
  
      const { error } = await stripe!.redirectToCheckout({
        mode: 'payment',
        lineItems: cart.map((cartItem) => ({
          price: cartItem.price.id,
          quantity: 1,
        })),
        successUrl: `https://justrunit-pl.github.io/sklep-test/`,
        cancelUrl: `https://justrunit-pl.github.io/sklep-test/`,
      });
  
      if (error) {
        console.warn('Error:', error);
        setLoading(false);
      }
    }
  };

  return (
    <>
      {items.map((item) => (
        <div key={item.id}>
          <input
            type="checkbox"
            id={item.id}
            value={item.id}
            onChange={(e) => e.target.checked ? setCart((cart: any) => [...cart, item]) : setCart((cart: any) => cart.filter((cartItem: any) => cartItem.id !== item.id))}
          />
          <label htmlFor={item.id}>{item.name} ({item.price.unit_amount / 100} zł)</label>
        </div>
      ))}
      <p>Suma: {cart.reduce((prev, curr) => prev + curr.price.unit_amount / 100, 0)} zł</p>
      <button
        disabled={loading}
        onClick={redirectToCheckout}
      >
        Buy the the equipment!
      </button>
    </>
  );
};

export { Checkout };
